<template>
	<div class="clearfix" id="UploadImg">
		<!--判断是否送礼页面-->
		<template v-if="$route.name == 'SendGift'">
			<ul>
				<li v-for="(item,index) in imgList">
					<img class="publish-icon" :src="item" style="width:2.8rem;height:2.8rem;"/>
					<div class="del-btn" @click="delImg(item,index)">
						<img src="../../assets/del-icon.png" />
					</div>
				</li>
				<li v-show="imgList.length<maxImgNum">
					<img class="publish-icon" src="../../assets/common_btn_add_pic@2x.png" style="width:2.8rem;height:2.8rem;"/>
					<input type="file" accept="image/*" name="file[]" id="publish-imgt"  @change="uploadImg($event)" ref="imgInput" />
				</li>
			</ul>
		</template>
		
		<template v-else>
			<ul>
				<li v-for="(item,index) in imgList">
					<img class="publish-icon" :src="item" />
					<div class="del-btn" @click="delImg(item,index)">
						<img src="../../assets/del-icon.png" />
					</div>
				</li>
				<li v-show="imgList.length<maxImgNum">
					<img class="publish-icon" src="../../assets/common_btn_add_pic@2x.png" />
					<input type="file" accept="image/*" name="file[]" id="publish-imgt"  @change="uploadImg($event)" ref="imgInput" />
				</li>
			</ul>
		</template>

	</div>
</template>

<script type="text/javascript">
	export default {
		props: {
			maxImgNum: {
				type: Number,
				default: 6
			}
		},
		data() {
			return {
				imgList: [],
				fileList: [],
				urlList: [],
				upLoadUrlList: [],
				imgIdList: [],
			}
		},
		computed: {

		},
		methods: {
			selectStar(index) {
				this.starIndex = index;
			},
			//上传图片
			uploadImg() {
				var oFile = document.getElementById("publish-imgt").files[0];
				var filename = oFile.name

				this.fileList.push(oFile)

				this.oFile = oFile

				let params = {
					a: 1,
					type: 1,
					filename: filename,
					oFile: oFile,
				}
				this.getExternalUploadObject(params)

				let oFReader = new FileReader();
				oFReader.readAsDataURL(oFile);
				oFReader.onload = (oFREvent) => {
					let imgData = oFREvent.currentTarget.result
					this.imgList.push(imgData)
					//把值清空
					oFile.value = '';
					document.getElementById("publish-imgt").value = ''
				};
			},
			delImg(item, index) {
				this.imgList.splice(index, 1)
				this.urlList.splice(index, 1)
				this.fileList.splice(index, 1)
				this.imgIdList.splice(index, 1)

			},
			//获取图片url
			getExternalUploadObject(params) {
				this.$Api.getExternalUploadObject(params).then((res) => {
					console.log(params)
					if(res.q.s == 0) {
						this.cos = res.q.cos
						let urlObj = {
							accessUrl: res.q.cos.accessUrl,
							sourceUrl: res.q.cos.sourceUrl
						}
						//						console.log(urlObj)
						//						this.urlList.push(urlObj)
						//						this.upLoadUrlList.push(res.q.cos.url)

						//上传图片到腾讯云
						this.$upLoadToCos(res.q.cos.url,params.oFile)
						this.getExternalFilesSubmit(urlObj)
						
					}
				})
			},
			getExternalFilesSubmit(urlObj) {
				let fileInfos = []
				fileInfos.push(urlObj)

				//				this.urlList.forEach((item) => {
				//					let fileInfo = {
				//						accessUrl: item.accessUrl,
				//						sourceUrl: item.sourceUrl
				//					}
				//					fileInfos.push(fileInfo)
				//				})
				console.log(fileInfos)
				this.$Api.getExternalFilesSubmit(1, 1, fileInfos).then((res) => {
					console.log(res)
					if(res.q.s == 0) {
						let files = res.q.files
						files.forEach((item) => {
							console.log(item.id)
							this.imgIdList.push(item.id)
						})
						this.$emit('getIds', this.imgIdList)
						//						for(let i = 0; i < this.fileList.length; i++) {
						//							$this.$upLoadToCos(this.upLoadUrlList[i], this.fileList[i])
						//						}
						//						this.getCommentSubmit()
					}
				})
			},
		}

	}
</script>

<style lang="scss">
	#UploadImg {
		padding: 0.51rem 0 0.55rem 0.4rem;
		ul {
			>li {
				position: relative;
				float: left;
				margin-right: 0.4rem;
				margin-bottom: 0.4rem;
			}
			.del-btn {
				position: absolute;
				top: 0;
				right: 0;
				width: 0.43rem;
				height: 0.43rem;
				background: rgba(0, 0, 0, 0.3);
			}
			input {
				position: absolute;
				top: 0;
				left: 0;
				opacity: 0;
				width: 2.0rem;
				height: 2.0rem;
			}
		}
		.publish-icon {
			width: 2.0rem;
			height: 2.0rem;
		}
	}
</style>


