<template>
	<div id="promoteInfo">
		<commonHeader :title="'个人信息'"></commonHeader>
    	<Cell title="真实姓名" type="input" placeholder="真实姓名，需与身份证一致" v-model="username"/>
			<Cell :title="'性别'" @click="openSex" :value="sex == 1?'男': sex==2 ? '女':''" />
    	<Cell title="我的职业" type="input" placeholder="请输入职业" v-model="job"/>
    	<Cell title="身份证号码" type="idCard" placeholder="真实姓名，需与身份证一致" v-model="idcode"/>
			
			<div class="apply__identity border" style="border-top: 1px solid #eee;">
	      <div class="apply__identity__title title">身份认证</div>
	      <div class="apply__identity__upload">
	        <div class="upload">
	          <UploadImgs @getIds="getIdImg" :maxImgNum='1'/>
	          <div class="upload__text">身份证正面照</div>
	        </div>
	        <div class="upload">
	          <UploadImgt @getIds="getStuImg" :maxImgNum='1'/>
	          <div class="upload__text">学生证</div>
	        </div>
	      </div>
	    </div>

	    <div class="apply__tips" style="border-bottom: 1px solid #eee;">
	      <p class="apply__tips__text">温馨提示</p>
	      <p class="apply__tips__text">1.为了保证您的认证顺利，请保证信息清晰可见。</p>
	      <p class="apply__tips__text">2.您提交的信息仅用于申请创业基金认证，请放心上传。</p>
	    </div>
			
    	<Cell title="邮箱" type="input" placeholder="请填写邮箱" v-model='email'/>
    	<Cell title="手机号码" type="tel" placeholder="请填写手机号码" v-model='mobile'/>
    	
    	<div class="sms__code" style="border: 1px solid #eee;">
    		<input type="text" placeholder="请输入验证码" v-model="code"/>
    		<span class="get-code" @click="getCode">获取验证码</span>
    	</div>
    	
    	
    	<div class="footer-btn active" @click="getMarketerApplySubmit">
    		提交资料
    	</div>
		
			<van-actionsheet v-model="showSex" :actions="actions" :cancel-text="'取消'" />
	</div>
</template>

<script>
import Cell from '@/components/Cell/Cell';
import MyTextArea from '@/components/TextArea/TextArea';
import UploadImgs from '@/components/UploadImgs/UploadImgs'
import UploadImgt from '@/components/UploadImgt/UploadImgt'
export default {
	data(){
		return {
			showSex:false,
			actions: [{
				name: '男',
				callback: () => {
					this.selectSex(1)
				}
			},
			{
				name: '女',
				callback: () => {
					this.selectSex(2)
				}
			}],
			username:'',
			sex: '',
			idcode:'',
			job:'',
			imgIdList:[],
			imgIdLis2:[],
			email:'',
			mobile:'',
			code:'',
			checkKey:'',
		}
	},
	copmuted:{
		isFinished(){
			if(this.username !='' && this.sex !='' && this.idcode !='' && this.email !='' && this.mobile !='' && this.imgIdList.length > 0 && this.imgIdLis2.length>0){
				return true
			}
			return false
		}
	},
	methods:{
		//选择性别
		openSex() {
			this.showSex = true;
		},
		//选择性别
		selectSex(type) {
			if(type == 1) {
				this.sex = 1
			} else {
				this.sex = 2
			}
			this.showSex = false;
		},
		getIdImg(ids){
  		this.imgIdList = ids
  	},
    getStuImg(ids){
  		this.imgIdLis2 = ids
  	},
  	//获取验证码
		getSmsCode() {
      let param = {
        a: 1,
        type: 12,
        mobile: this.mobile,
      }
      this.$Api.SMSCode(param).then((res) => {
        if(res.q.s == 0) {
          this.$toast('发送成功!');
          this.checkKey = res.q.checkKey;
        }else {
          this.$toast(res.q.d);
        }
      })
		},
		//获取验证码
		getCode() {
			if(this.mobile.length == 11) {
				if(this.count > 0) {
					return;
				} else {
					this.count = 10;
					this.isSend = true
					this.countDown()
					this.getSmsCode()
				}
			} else {
				this.$toast('请输入正确手机号')
			}
		},
		countDown() {
			var cd = setInterval(() => {
				this.count--;
				if(this.count <= 0) {
					this.count = 0;
					this.isSend = false
					clearInterval(cd);
				}
			}, 1000)
		},
		submitCode() {
      let codeParam = {
        a: 2,
        type: 12,
        mobile: this.mobile,
        checkKey:this.checkKey,
        code: this.code
      }

      this.$Api.SMSCode(codeParam).then((res) => {
        if(res.q.s == 0) {
//      	this.getUserBindingSubmit()
        }else {
          this.$toast('手机验证码出错')
        }
      })
    },
    getMarketerApplySubmit(){
    	if(this.username !='' && this.sex !='' && this.idcode !='' && this.email !='' && this.mobile !='' && this.imgIdList.length > 0 && this.imgIdLis2.length>0){
    		let checkKey = this.checkKey
    		let params = {
    			name : this.name,
    			sex:this.sex,
    			job:this.job,
    			idCardImage:{
    				code:this.idcode,
    				imageFontId:this.imgIdList[0],
    				ownerImageId:this.imgIdLis2[0],
    			},
    			email:this.email
    		}
    		this.$Api.getMarketerApplySubmit(1,checkKey,params).then((res)=>{
    			console.log(res)
    			if(res.q.s == 0){
    				this.$toast('申请成功')
    				setTimeout(()=>{
    					this.$router.replace({name:'Promote'})
    				},1500)
    			}else{
    				this.$toast(res.q.d)
    			}
    		})
    	}else{
    		this.$toast('请填写完整信息')
    	}
    }
	},
  components: {
  	Cell,
  	MyTextArea,
  	UploadImgt,
  	UploadImgs
  },
  
}
</script>

<style lang="scss">
	@import "../../../../sass/baseSass.scss";
	#promoteInfo{
		padding-bottom: 1.35rem;
		.apply__text {
      border-top: 1px solid #eee;
      padding-bottom: .2rem;
    }

    .title {
      padding: .4rem 0 .3rem 0;
      padding-left: .4rem;
      font-size: 0.427rem;
    }
		#UploadImg {
      padding-top: 0;
      padding-bottom: 0;
    }
    .apply__identity {
    	
      #UploadImg {
        display: inline-block;
        ul > li {
          margin-bottom: 0;
        }
      }
      .apply__identity__upload {
        display: flex;
        .upload {
          width: 50%;
          .upload__text {
            padding: .1rem 0;
            padding-left: .4rem;
            font-size: 0.37rem;
            color: #666666;
          }
        }
      }
		}
    .apply__tips {
      padding: .4rem 0 0 .4rem;
      line-height: .53rem;
      font-size: 0.37rem;
      color: #666666;
    }
    .sms__code{
    	position: relative;
    	width: 100%;
    	height: 1.2rem;
    	line-height: 1.2rem;
    	font-size: 0.427rem;
    	color: #333;
    	padding: 0 0.4rem;
    	>input{
    		width: 74%;
    	}
    	.get-code{
		    width: 2.5rem;
		    height: 0.6rem;
		    line-height: 0.6rem;
		    text-align: center;
		    font-size: 0.37rem;
		    color: #de7fef;
		    position: absolute;
		    right: 0;
		    top: 50%;
		    transform: translateY(-50%);
		    border-left: 1px #d8d8d8 solid;
    	}
    }
    .footer-btn{
    	position:fixed;
    	font-size: 0.45rem;
    	bottom: 0;
    	width: 100%;
    	height: 1.31rem;
    	line-height: 1.31rem;
    	color: #fff;
    	text-align: center;
    	background: #ccc;
    	
    }
    .active{
    	@include bg-color;
    }
	}
</style>


